@import url(https://fonts.googleapis.com/css2?family=Orbitron:wght@600&display=swap);
*{box-sizing:border-box}body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;display:flex;justify-content:center;align-items:center}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.form{margin:20px auto;width:300px;text-align:center}.form label{margin:5px auto}.form input{display:flex;margin:5px auto}.form button{margin-top:15px;width:150px;font-size:17px}.App{border:3px solid;border-right-color:#999;border-bottom-color:#999;border-left-color:#fff;border-top-color:#fff;background-color:#c2c2c2;padding:16px}.Header{border:3px solid;border-right-color:#fff;border-bottom-color:#fff;border-left-color:#7b7b7b;border-top-color:#7b7b7b;background-color:#c0c0c0;padding:5px 10px;display:flex;justify-content:space-between;align-items:center}.Body{border:3px solid;border-right-color:#fff;border-bottom-color:#fff;border-left-color:#7b7b7b;border-top-color:#7b7b7b;margin-top:16px}.face{border:3px solid;border-right-color:#999;border-bottom-color:#999;border-left-color:#fff;border-top-color:#fff;width:50px;height:45px;font-size:25px;padding-bottom:5px;display:flex;justify-content:center;align-items:center;cursor:pointer}.face:active{border:3px solid;border-right-color:#fff;border-bottom-color:#fff;border-left-color:#7b7b7b;border-top-color:#7b7b7b}

.number-display{font-family:'Orbitron', sans-serif;width:80px;height:40px;color:#ff0701;background-color:black;font-size:30px;text-align:center}

.button{border:3px solid;border-right-color:#7b7b7b;border-bottom-color:#7b7b7b;border-left-color:#fff;border-top-color:#fff;width:30px;height:30px;display:flex;justify-content:center;align-items:center;font-weight:bold}.button:active{border:3px solid;border-right-color:#fff;border-bottom-color:#fff;border-left-color:#7b7b7b;border-top-color:#7b7b7b}.button.visible{border-color:#7b7b7b;border-width:1px}.button.red{background-color:red}.button span{font-size:16px}.button.value-1{color:blue}.button.value-2{color:green}.button.value-3{color:red}.button.value-4{color:purple}.button.value-5{color:maroon}.button.value-6{color:turquoise}.button.value-7{color:black}.button.value-8{color:gray}

